import React, { useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Card,
  CardContent,
  CardActions,
  Button,
  Grid,
  Divider,
  Chip,
  Paper,
  Tabs,
  Tab,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  TextField,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Switch,
  FormControlLabel
} from '@mui/material';
import { styled } from '@mui/material/styles';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import LockIcon from '@mui/icons-material/Lock';
import SecurityIcon from '@mui/icons-material/Security';
import WarningIcon from '@mui/icons-material/Warning';
import { Link as RouterLink } from 'react-router-dom';

// Mock card data
const cards = [
  {
    id: 1,
    name: 'Cash Rewards Credit Card',
    type: 'Credit',
    lastFour: '1234',
    expiryDate: '05/25',
    availableCredit: 7500,
    usedCredit: 2500,
    status: 'Active',
    isVirtual: false,
    cardHolder: 'John Smith',
    rewards: 325,
    limit: 10000
  },
  {
    id: 2,
    name: 'Premium Travel Card',
    type: 'Credit',
    lastFour: '5678',
    expiryDate: '09/26',
    availableCredit: 15000,
    usedCredit: 4200,
    status: 'Active',
    isVirtual: false,
    cardHolder: 'John Smith',
    rewards: 1240,
    limit: 20000
  },
  {
    id: 3,
    name: 'Health Savings Debit',
    type: 'Debit',
    lastFour: '9012',
    expiryDate: '12/24',
    balance: 2340.75,
    status: 'Pending Activation',
    isVirtual: true,
    cardHolder: 'John Smith'
  },
  {
    id: 4,
    name: 'Business Expense Card',
    type: 'Credit',
    lastFour: '3456',
    expiryDate: '08/25',
    availableCredit: 25000,
    usedCredit: 12000,
    status: 'Temporarily Locked',
    isVirtual: false,
    cardHolder: 'Smith Business LLC',
    rewards: 780,
    limit: 30000
  }
];

// Card color mapping
const cardColors: {[key: string]: {background: string, text: string}} = {
  'Credit': {
    background: 'linear-gradient(135deg, #2196f3 0%, #0d47a1 100%)',
    text: '#ffffff'
  },
  'Debit': {
    background: 'linear-gradient(135deg, #4caf50 0%, #1b5e20 100%)',
    text: '#ffffff'
  },
  'Prepaid': {
    background: 'linear-gradient(135deg, #ff9800 0%, #e65100 100%)',
    text: '#ffffff'
  }
};

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

const TabPanel = (props: TabPanelProps) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`card-tabpanel-${index}`}
      aria-labelledby={`card-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
};

const StyledCard = styled(Card)(({ theme }) => ({
  position: 'relative',
  margin: theme.spacing(1),
  height: 200,
  borderRadius: theme.spacing(1),
  transition: 'transform 0.3s ease-in-out',
  '&:hover': {
    transform: 'scale(1.02)',
    boxShadow: theme.shadows[8]
  }
}));

const CardManagement: React.FC = () => {
  const [tabValue, setTabValue] = useState(0);
  const [showFullCardNumber, setShowFullCardNumber] = useState<{[key: number]: boolean}>({});
  const [lockCardDialogOpen, setLockCardDialogOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState<number | null>(null);
  const [reportLostDialogOpen, setReportLostDialogOpen] = useState(false);
  const [requestLimitDialogOpen, setRequestLimitDialogOpen] = useState(false);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const toggleCardVisibility = (cardId: number) => {
    setShowFullCardNumber(prev => ({ 
      ...prev, 
      [cardId]: !prev[cardId] 
    }));
  };

  const handleLockCard = (cardId: number) => {
    setSelectedCard(cardId);
    setLockCardDialogOpen(true);
  };

  const handleReportLost = (cardId: number) => {
    setSelectedCard(cardId);
    setReportLostDialogOpen(true);
  };

  const handleRequestLimit = (cardId: number) => {
    setSelectedCard(cardId);
    setRequestLimitDialogOpen(true);
  };

  const closeLockCardDialog = () => {
    setLockCardDialogOpen(false);
    setSelectedCard(null);
  };

  const closeReportLostDialog = () => {
    setReportLostDialogOpen(false);
    setSelectedCard(null);
  };

  const closeRequestLimitDialog = () => {
    setRequestLimitDialogOpen(false);
    setSelectedCard(null);
  };

  // Filter cards based on tab
  const getFilteredCards = () => {
    if (tabValue === 0) return cards; // All cards
    if (tabValue === 1) return cards.filter(card => card.type === 'Credit');
    if (tabValue === 2) return cards.filter(card => card.type === 'Debit');
    if (tabValue === 3) return cards.filter(card => card.status === 'Pending Activation');
    return [];
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Typography variant="h4" gutterBottom>
        Card Management
      </Typography>
      
      <Paper sx={{ width: '100%', mb: 4 }}>
        <Tabs 
          value={tabValue} 
          onChange={handleTabChange}
          variant="fullWidth"
          indicatorColor="primary"
          textColor="primary"
        >
          <Tab label="All Cards" />
          <Tab label="Credit Cards" />
          <Tab label="Debit Cards" />
          <Tab label="Pending Activation" />
        </Tabs>
        
        <TabPanel value={tabValue} index={tabValue}>
          <Typography variant="subtitle1" gutterBottom>
            {tabValue === 0 ? 'All Your Cards' : 
             tabValue === 1 ? 'Your Credit Cards' : 
             tabValue === 2 ? 'Your Debit Cards' : 'Cards Pending Activation'}
          </Typography>

          <Grid container spacing={3}>
            {getFilteredCards().map(card => (
              <Box 
                key={card.id}
                sx={{ 
                  width: { xs: '100%', sm: '50%', md: '33.333%' }, 
                  p: 1.5 
                }}
              >
                <StyledCard 
                  elevation={4}
                  sx={{
                    background: cardColors[card.type].background,
                    color: cardColors[card.type].text,
                    opacity: card.status === 'Temporarily Locked' ? 0.8 : 1,
                  }}
                >
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                      <Typography variant="h6" component="div" sx={{ mb: 2 }}>
                        {card.name}
                      </Typography>
                      {card.isVirtual && (
                        <Chip 
                          size="small" 
                          label="Virtual" 
                          sx={{ bgcolor: 'rgba(255,255,255,0.2)', color: 'inherit' }} 
                        />
                      )}
                    </Box>

                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                      <Typography variant="body1">
                        {showFullCardNumber[card.id] ? 
                          "4012 8888 8888 " + card.lastFour : 
                          "•••• •••• •••• " + card.lastFour
                        }
                      </Typography>
                      <IconButton 
                        size="small" 
                        onClick={() => toggleCardVisibility(card.id)}
                        sx={{ ml: 1, color: 'inherit', opacity: 0.8 }}
                      >
                        {showFullCardNumber[card.id] ? <VisibilityOffIcon /> : <VisibilityIcon />}
                      </IconButton>
                    </Box>

                    <Typography variant="body2" sx={{ mb: 1 }}>
                      Expires: {card.expiryDate}
                    </Typography>
                    
                    <Typography variant="body2">
                      {card.cardHolder}
                    </Typography>

                    {card.status === 'Temporarily Locked' && (
                      <Chip 
                        icon={<LockIcon />} 
                        label="Locked" 
                        size="small"
                        sx={{ position: 'absolute', top: 10, right: 10, bgcolor: 'rgba(255,255,255,0.3)', color: 'inherit' }}
                      />
                    )}
                  </CardContent>
                  <CardActions sx={{ position: 'absolute', bottom: 0, right: 0 }}>
                    {card.status === 'Pending Activation' ? (
                      <Button 
                        size="small" 
                        color="warning" 
                        variant="contained"
                        component={RouterLink} 
                        to="/activate"
                        sx={{ bgcolor: 'white', color: 'warning.main', '&:hover': { bgcolor: 'rgba(255,255,255,0.8)' } }}
                      >
                        Activate Now
                      </Button>
                    ) : (
                      <>
                        {card.status === 'Temporarily Locked' ? (
                          <Button 
                            size="small" 
                            variant="contained"
                            onClick={() => handleLockCard(card.id)}
                            sx={{ bgcolor: 'rgba(255,255,255,0.5)', color: 'inherit', '&:hover': { bgcolor: 'rgba(255,255,255,0.7)' } }}
                          >
                            Unlock
                          </Button>
                        ) : (
                          <Button 
                            size="small" 
                            variant="contained"
                            onClick={() => handleLockCard(card.id)}
                            sx={{ bgcolor: 'rgba(255,255,255,0.5)', color: 'inherit', '&:hover': { bgcolor: 'rgba(255,255,255,0.7)' } }}
                          >
                            Lock
                          </Button>
                        )}
                      </>
                    )}
                  </CardActions>
                </StyledCard>

                <Paper elevation={2} sx={{ mt: 1, p: 2 }}>
                  <Typography variant="subtitle2" gutterBottom>
                    {card.type === 'Credit' ? 'Card Details' : 'Account Details'}
                  </Typography>
                  
                  {card.type === 'Credit' && (
                    <>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2" color="text.secondary">Available Credit:</Typography>
                        <Typography variant="body2">${card.availableCredit?.toLocaleString()}</Typography>
                      </Box>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2" color="text.secondary">Used Credit:</Typography>
                        <Typography variant="body2">${card.usedCredit?.toLocaleString()}</Typography>
                      </Box>
                      {card.rewards && (
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                          <Typography variant="body2" color="text.secondary">Reward Points:</Typography>
                          <Typography variant="body2">{card.rewards.toLocaleString()} pts</Typography>
                        </Box>
                      )}
                    </>
                  )}
                  
                  {card.type === 'Debit' && (
                    <>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                        <Typography variant="body2" color="text.secondary">Current Balance:</Typography>
                        <Typography variant="body2">${card.balance?.toLocaleString()}</Typography>
                      </Box>
                    </>
                  )}
                  
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
                    <Button 
                      size="small" 
                      startIcon={<SecurityIcon />}
                      onClick={() => handleReportLost(card.id)}
                    >
                      Report Lost
                    </Button>
                    
                    {card.type === 'Credit' && (
                      <Button 
                        size="small" 
                        startIcon={<AccountBalanceWalletIcon />}
                        onClick={() => handleRequestLimit(card.id)}
                      >
                        Request Limit
                      </Button>
                    )}
                  </Box>
                </Paper>
              </Box>
            ))}
          </Grid>
        </TabPanel>
      </Paper>

      {/* Lock/Unlock Card Dialog */}
      <Dialog open={lockCardDialogOpen} onClose={closeLockCardDialog}>
        <DialogTitle>
          {cards.find(c => c.id === selectedCard)?.status === 'Temporarily Locked' ? 'Unlock Card' : 'Lock Card'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {cards.find(c => c.id === selectedCard)?.status === 'Temporarily Locked' ? 
              'Are you sure you want to unlock your card ending in ' :
              'Are you sure you want to temporarily lock your card ending in '
            }
            <strong>{cards.find(c => c.id === selectedCard)?.lastFour}</strong>?
            <br /><br />
            {cards.find(c => c.id === selectedCard)?.status === 'Temporarily Locked' ? 
              'Unlocking will allow transactions to be processed again.' :
              'Temporarily locking your card will prevent any new transactions until you unlock it.'
            }
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={closeLockCardDialog}>Cancel</Button>
          <Button onClick={closeLockCardDialog} variant="contained" color="primary" autoFocus>
            {cards.find(c => c.id === selectedCard)?.status === 'Temporarily Locked' ? 'Unlock Card' : 'Lock Card'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Report Lost Card Dialog */}
      <Dialog open={reportLostDialogOpen} onClose={closeReportLostDialog}>
        <DialogTitle>Report Lost or Stolen Card</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Report your card ending in <strong>{cards.find(c => c.id === selectedCard)?.lastFour}</strong> as lost or stolen.
            <br /><br />
            This will permanently deactivate this card and we'll send you a replacement.
          </DialogContentText>
          <FormControl fullWidth margin="dense">
            <InputLabel>Reason</InputLabel>
            <Select defaultValue="lost">
              <MenuItem value="lost">Lost card</MenuItem>
              <MenuItem value="stolen">Stolen card</MenuItem>
              <MenuItem value="damaged">Damaged card</MenuItem>
              <MenuItem value="fraud">Suspicious activity</MenuItem>
            </Select>
          </FormControl>
          <TextField
            margin="dense"
            label="Additional Information"
            fullWidth
            multiline
            rows={3}
          />
          <FormControlLabel
            control={<Switch color="primary" />}
            label="Rush delivery (additional fee applies)"
            sx={{ mt: 2 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={closeReportLostDialog}>Cancel</Button>
          <Button 
            onClick={closeReportLostDialog} 
            variant="contained" 
            color="error" 
            startIcon={<WarningIcon />}
            autoFocus
          >
            Report and Replace
          </Button>
        </DialogActions>
      </Dialog>

      {/* Request Credit Limit Dialog */}
      <Dialog open={requestLimitDialogOpen} onClose={closeRequestLimitDialog}>
        <DialogTitle>Request Credit Limit Change</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Request a credit limit change for your card ending in <strong>{cards.find(c => c.id === selectedCard)?.lastFour}</strong>.
            <br /><br />
            Current limit: <strong>${cards.find(c => c.id === selectedCard)?.limit?.toLocaleString()}</strong>
          </DialogContentText>
          <TextField
            margin="dense"
            label="Requested Limit"
            fullWidth
            type="number"
            inputProps={{ min: 1000, step: 1000 }}
            defaultValue={cards.find(c => c.id === selectedCard)?.limit}
          />
          <TextField
            margin="dense"
            label="Annual Income"
            fullWidth
            type="number"
            inputProps={{ min: 0, step: 1000 }}
            helperText="This helps us evaluate your request"
          />
          <TextField
            margin="dense"
            label="Reason for Increase (Optional)"
            fullWidth
            multiline
            rows={2}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={closeRequestLimitDialog}>Cancel</Button>
          <Button onClick={closeRequestLimitDialog} variant="contained" color="primary" autoFocus>
            Submit Request
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default CardManagement; 